"use client";

import "./globals.css";
import Navbar from "@/components/navbar";
import { usePathname } from "next/navigation";

export const metadata = {
  title: "The Urban Forge",
  description: "Delicious meals delivered fast.",
  manifest: "/manifest.json",
  themeColor: "#1d1d1d",
};

export default function RootLayout({ children }) {
  const pathname = usePathname();

  // âœ… Only show Navbar on specific pages
  const showNavbar = ["/home", "/orders", "/settings"].includes(pathname);

  return (
    <html lang="en">
      <body className="bg-[#1d1d1d] text-white relative min-h-screen flex flex-col">
        {/* Main content */}
        <main className="flex-grow pb-6">{children}</main>

        {/* Floating Navbar (only on selected pages) */}
        {showNavbar && (
          <div className="fixed bottom-4 left-1/2 -translate-x-1/2 z-50">
            <Navbar />
          </div>
        )}
      </body>
    </html>
  );
}
